import React, {useCallback, useMemo} from "react";
import styled from "styled-components"
import {GatsbyImage, getImage} from "gatsby-plugin-image";
import {colorBrand, colorGray75, colorWhite} from "../setting/theme";
import "tiny-slider/dist/tiny-slider.css";
import {CssIcon} from "../patterns/icon";
import {Button, ButtonSmallStyle} from "../patterns/button";
import arrow from '../images/icons/arrow-right.svg';
import { tns } from  'tiny-slider/src/tiny-slider';


const Wrapper = styled.div`
    height: 700px;

    .tns-controls {
        position: absolute;
        bottom: 20px;
        right: 20px;

        &:focus {
            outline: 0;
        }

        button {
            ${ButtonSmallStyle}

            &:focus {
                outline: 0;
            }

            &:first-child {
                display: none;
            }
        }
    }

    .tns-nav {
        position: absolute;
        right: 72px;
        bottom: 30px;

        button {
            width: 20px;
            height: 20px;
            padding: 5px;
            border-radius: 100%;
            background-color: ${colorGray75};
            background-clip: content-box;
            border: 0;

            &:focus {
                outline: 0;
            }

            &.tns-nav-active {
                background-color: ${colorBrand};
            }
        }
    }

    .tns-outer:nth-child(1),
    .image-slider:not(.tns-slider) {
        position: absolute;
        left: 0;
        right: 0;
        height: 700px;
        max-width: 1400px;
        margin: 0 auto;
        overflow: hidden;

        img {
            height: 700px;
            object-fit: cover;
        }
    }

    .tns-outer:nth-child(2),
    .text-slider:not(.tns-slider) {
        position: relative;
        padding: 20px !important;
        width: 300px;
        height: 400px;
        top: 100px;
        background-color: ${colorWhite};
        border-bottom: 3px solid ${colorBrand};
    }

    .text-slider__item {
        h2 {
            margin-top: 0;
        }
    }
`;

const Slider = ({slides}) => {
  const imageSliderRef = useCallback(
  async node => {
      if (node instanceof HTMLElement) {
        tns({
          container: node,
          items: 1,
          nav: false, // no dots
          controls: false, //no arrows
          lazyload: true,
          rewind: true
        });
      }
    },
      [],
  );

  const textSliderRef = useCallback(
    async node => {
      if (tns && node instanceof HTMLElement) {
        tns({
          container: node,
          items: 1,
          rewind: true,
          controlsText: ['', `<span style="${CssIcon} background-image: url(${arrow});"></span>`]
        });
      }
    },
    [],
  );

  return (
    <Wrapper>
      <div className="image-slider" ref={imageSliderRef}>
        {slides.map(slide => {
          const image = getImage(slide.image);

          return (
            <div className="image-slider__item">
              <GatsbyImage alt={slide.title} image={image}/>
            </div>
            )
        })}

      </div>
      <div className="text-slider" ref={textSliderRef}>
        {slides.map(slide => (
          <div className="text-slider__item">
            <h2>{slide.title}</h2>
            <p dangerouslySetInnerHTML={{__html: slide.text}}/>
          </div>
        ))}
      </div>
    </Wrapper>
  )
}

export default Slider;
